<template>
    <div class="add-users container">
       <Alert v-if="alert" v-bind:message="alert"></Alert>
       <h2>编辑用户</h2>
       <form class="form-group well" v-on:submit="updatedCustomer">
          <label>姓名</label>
          <input type="text" class="form-control is-valid" placeholder="姓名" v-model="customer.name"> 
          <label>电话</label>
          <input type="text" class="form-control is-valid" placeholder="电话" v-model="customer.phone">
          <label>邮件</label>
          <input type="text" class="form-control is-valid" placeholder="邮箱" v-model="customer.email">
          <label>年龄</label>
          <input type="text" class="form-control is-valid" placeholder="年龄" v-model="customer.age">
          <button type="submit" class="btn btn-success">提交</button>
       </form>
    </div>
</template>

<script>
import Alert from "./alert"
export default{ 
    name:"add-users",
    data () {
        return {
           customer:[],
           alert:""
        }
    },
    methods:{
        getCustomer(id){
           this.$http.get('https://wd4440641298eabqtv.wilddogio.com/users/'+id+'.json')
           .then(function(response){
              this.customer = response.body;
           })
        },
        updatedCustomer(e){
           if(!this.customer.name || !this.customer.phone ||!this.customer.email){
               this.alert = "请输入完整的信息";
           }else{
               let updatedCustomer = {
                  name:this.customer.name,
                  phone:this.customer.phone,
                  email:this.customer.email,
                  age:this.customer.age
               }
               this.$http.put("https://wd4440641298eabqtv.wilddogio.com/users/"+this.$route.params.id+".json",updatedCustomer)
               .then(function(response){
                   this.$router.push({path:'/',query:{alert:"用户信息更新成功"}});
                   //query把alert传到show，然后通过show通过message传给alert.vue,然后显示
               })
               e.preventDefault();
           }
           e.preventDefault();
        }
    },
    created() {
        this.getCustomer(this.$route.params.id);
    },
    components:{
        Alert
    }
}

</script>

<style scoped>

</style>


