<template>
    <div class="detail container">
       <router-link to="/" class="btn btn-success">返回</router-link>
       <h1 class="page-header">
           {{customer.name}}
       <span class="pull-right">
          <!-- <router-link class="btn btn-primary" v-bind:to="'/edit/'+customer.id">编辑</router-link> -->
          <button class="btn btn-danger" v-on:click="deleteCustomer(customer.id)">删除</button>
       </span>
       </h1>
       <ul class="list-group">
          <li class="list-group-item"> {{customer.phone}}</li>
          <li class="list-group-item"> {{customer.email}}</li>
          <li class="list-group-item"> {{customer.age}}</li>
       </ul>
    </div>
</template>

<script>
export default{
    name:"detail",
    data () {
        return {
           customer:[],
        }
    },
    methods:{
        getCustomer(id){
            this.$http.get('https://wd4440641298eabqtv.wilddogio.com/users/'+id+".json")
            .then(function(response){
               this.customer = response.body;
            })
        },
        deleteCustomer(){
            this.$http.delete('https://wd4440641298eabqtv.wilddogio.com/users/'+this.$route.params.id + ".json")
            .then(function(response){
                this.$router.push({path:'/',query:{alert:"用户信息删除成功"}});
            })
        }
    },
    created(){
        this.getCustomer(this.$route.params.id);
    }

}

</script>

<style>

</style>


