import Add from '../components/add-users.vue'
import Detail from '../components/detail-users.vue'
import Show from '../components/show-users.vue'
import Edit from '../components/edit-users.vue'

export default[
    {path:'/',component:Show},
    {path:'/add',component:Add},
    {path:'/detail/:id',component:Detail},
    {path:'/edit/:id',component:Edit}
]



