<template>
    <div class="show container">
        <Alert v-if="alert" v-bind:message="alert"></Alert>
        <h1>用户管理系统</h1>
        <input type="text" class="form-control" placeholder="搜索" v-model="search">
        <table class="table table-striped">
            <thead>
                <tr>
                    <th>姓名</th>
                    <th>电话</th>
                    <th>邮箱</th>                   
                </tr>
            </thead>
            <tbody>
                <tr v-for="customer in searchData(customers,search)">
                    <td>{{customer.name}}</td>
                    <td>{{customer.phone}}</td>
                    <td>{{customer.email}}</td>
                    <td class="bt"><router-link  class="btn btn-default" v-bind:to="'/detail/'+customer.id">详情</router-link></td>
                    <td class="by"><router-link class="btn btn-primary" v-bind:to="'/edit/'+customer.id">编辑</router-link></td>
                    
                </tr>
            </tbody>
        </table> 
    </div>
</template>

<script>
import Alert from './alert'
export default{
    name:"show",
    data () {
        return {
          customers:[],
          alert:"",
          search:""
        }
    },
    methods:{
        getCustomers() {
            this.$http.get("https://wd4440641298eabqtv.wilddogio.com/users.json")
            .then(function(response){
              return response.body
            })
            .then(function(data){
                var Arr = [];
                for(let key in data){
                    data[key].id = key;
                    Arr.push(data[key]); 
                }
                this.customers = Arr;
            })
        },
        searchData(customers,search){
           return customers.filter((customer)=>{
               return customer.name.match(search);
           })
        }
    },
    created() {
          if(this.$route.query.alert){
              this.alert = this.$route.query.alert;
          }        
        this.getCustomers();
    },
    updated() {
        this.getCustomers();
    },
    components:{
        Alert
    },

}

</script>

<style scoped>
.bt,.by{
    padding: 0;
    width: 20px;
    height: 10px;
}
</style>



